$footer-height: 100px
$footer-color: #F5F5F5

html
  position: relative
  min-height: 100%

body > .container
  margin-bottom: $footer-height

.footer
  position: absolute
  bottom: 0
  width: 100%
  height: $footer-height
  background-color: $footer-color

  .container
    width: auto
    max-width: 680px
    padding: 0 15px

    .text-muted
      margin: 20px 0
